<template>
  <div id="daily_operation_report">
    <h2>历史运营数据表格</h2>
    <div>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="渠道">
          <el-select v-model="form.channel" placeholder="请选择渠道">
            <el-option
              v-for="(item, index) in channels"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <div>
          <el-form-item label="月份">
            <el-date-picker
              format="yyyy 年 MM 月"
              value-format="yyyy-MM"
              v-model="form.monthPick"
              type="month"
              :picker-options="month_pickerOptions"
              placeholder="选择月份"
            >
            </el-date-picker>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button type="primary" @click="queryData">查询</el-button>
        </el-form-item>
      </el-form>

      <el-table
        highlight-current-row
        :cell-style="cellStyle"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        :data="tableData"
        style="width: 100%"
        max-height="750"
      >
        <el-table-column fixed width="100">
          <el-table-column prop="date" label="日期" width="100">
          </el-table-column>
        </el-table-column>
        <el-table-column
          v-for="(items, i) in theadData"
          :key="i"
          :label="items.title"
        >
          <el-table-column
            v-for="(item, j) in items.abbrName"
            :key="j"
            :prop="item.prop"
            :label="item.label"
            width="80"
          >
          </el-table-column>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      theadData: [
        {
          title: "中睿汇总",
          abbrName: [
            {
              prop: "zr_yye",
              label: "营业额合计"
            },
            {
              prop: "tb",
              label: "同比"
            },
            {
              prop: "zr_gghj",
              label: "广告合计"
            },
            {
              prop: "zr_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "胤佑美国",
          abbrName: [
            {
              prop: "yyus_yye",
              label: "营业额"
            },
            {
              prop: "yyus_gghf",
              label: "广告花费"
            },
            {
              prop: "yyus_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "胤佑加拿大",
          abbrName: [
            {
              prop: "yyca_yye",
              label: "营业额"
            },
            {
              prop: "yyca_gghf",
              label: "广告花费"
            },
            {
              prop: "yyca_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "爱瑙美国",
          abbrName: [
            {
              prop: "anus_yye",
              label: "营业额"
            },
            {
              prop: "anus_gghf",
              label: "广告花费"
            },
            {
              prop: "anus_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "胤佑日本",
          abbrName: [
            {
              prop: "yyjp_yye",
              label: "营业额"
            },
            {
              prop: "yyjp_gghf",
              label: "广告花费"
            },
            {
              prop: "yyjp_ggzb",
              label: "广告占比"
            }
          ]
        },
        // 利百锐日本
        {
          title: "利百锐日本",
          abbrName: [
            {
              prop: "lbrjp_yye",
              label: "营业额"
            },
            {
              prop: "lbrjp_gghf",
              label: "广告花费"
            },
            {
              prop: "lbrjp_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "中睿欧洲",
          abbrName: [
            {
              prop: "zreu_yye",
              label: "营业额"
            },
            {
              prop: "zreu_gghf",
              label: "广告花费"
            },
            {
              prop: "zreu_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "中睿英国",
          abbrName: [
            {
              prop: "zruk_yye",
              label: "营业额"
            },
            {
              prop: "zruk_gghf",
              label: "广告花费"
            },
            {
              prop: "zruk_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "京汇欧洲",
          abbrName: [
            {
              prop: "jheu_yye",
              label: "营业额"
            },
            {
              prop: "jheu_gghf",
              label: "广告花费"
            },
            {
              prop: "jheu_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "京汇英国",
          abbrName: [
            {
              prop: "jhuk_yye",
              label: "营业额"
            },
            {
              prop: "jhuk_gghf",
              label: "广告花费"
            },
            {
              prop: "jhuk_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "爱瑙欧洲",
          abbrName: [
            {
              prop: "aneu_yye",
              label: "营业额"
            },
            {
              prop: "aneu_gghf",
              label: "广告花费"
            },
            {
              prop: "aneu_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "爱瑙英国",
          abbrName: [
            {
              prop: "anuk_yye",
              label: "营业额"
            },
            {
              prop: "anuk_gghf",
              label: "广告花费"
            },
            {
              prop: "anuk_ggzb",
              label: "广告占比"
            }
          ]
        },

        {
          title: "胤佑澳洲",
          abbrName: [
            {
              prop: "yyau_yye",
              label: "营业额"
            },
            {
              prop: "yyau_gghf",
              label: "广告花费"
            },
            {
              prop: "yyau_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "胤佑墨西哥",
          abbrName: [
            {
              prop: "yymx_yye",
              label: "营业额"
            },
            {
              prop: "yymx_gghf",
              label: "广告花费"
            },
            {
              prop: "yymx_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "爱瑙加拿大",
          abbrName: [
            {
              prop: "anca_yye",
              label: "营业额"
            },
            {
              prop: "anca_gghf",
              label: "广告花费"
            },
            {
              prop: "anca_ggzb",
              label: "广告占比"
            }
          ]
        },
        {
          title: "利百锐美国",
          abbrName: [
            {
              prop: "lbrus_yye",
              label: "营业额"
            },
            {
              prop: "lbrus_gghf",
              label: "广告花费"
            },
            {
              prop: "lbrus_ggzb",
              label: "广告占比"
            }
          ]
        }
      ],
      tableData: [],
      zr: [],
      zr2: [],
      form: {
        channel: "",
        monthPick: ""
      },
      channels: [],
      month_pickerOptions: {
        disabledDate: function(time) {
          return time.getTime() > new Date().getTime();
        }
      }
    };
  },
  created() {
    this.$axios({
      url: this._url2 + "server/stock_management/is_open/",
      method: "get"
    }).then(res => {
      if (res.data.code == 200) {
        // 请求渠道
        let url = "server/personnel_management/get_sign_country/";
        this.$axios.get(this._url2 + url).then(res => {
          this.channels.push(res.data.data1);
        });
        // 请求数据
        let date = new Date();
        date = date.getFullYear() + "-" + (date.getMonth() + 1);
        this.getHistoryData(date);
      } else {
        alert("你没有权限进入这个页面");
        this.$router.push("/Login");
      }
    });
  },
  methods: {
    cellStyle({ row, column, rowIndex, columnIndex }) {
      // console.log(column);
      if (column.label.includes("营业额")) {
        return {
          background: "#ddd"
        };
      }
    },
    queryData() {
      if (!this.form.channel || !this.form.monthPick) {
        alert("请选择渠道和日期进行查询！");
        return;
      }
      console.log(this.form);
      // 发送查询请求
      this.getHistoryData(this.form.monthPick);
    },
    change() {},
    getHistoryData(date) {
      this.loading = true;

      this.$axios
        .get(this._url2 + "server/databases/get_history_report/", {
          params: {
            date
          }
        })
        .then(res => {
          this.zr = res.data.data.zr;
          this.zr2 = res.data.data.zr2;
          this.tableData = this.zr.concat(this.zr2);
          this.loading = false;
        });
    }
  }
};
</script>
<style>
#daily_operation_report {
  /* max-height: 84vh; */
  overflow: hidden;
}
#daily_operation_report .el-table .cell {
  text-align: center;
  font-size: 12px;
}
#daily_operation_report .el-table th > .cell {
  text-align: center;
  font-size: 14px;
}
#daily_operation_report .el-form {
  display: flex;
}
#daily_operation_report .el-form-item__label {
  color: #fff;
}
</style>
<style scoped>
#daily_operation_report {
  text-align: center;
}
h2 {
  margin: 0px auto;
  color: #fff;
}
.timepicker {
  font-size: 14px;
  margin-left: 80px;
}
#daily_operation_report > div {
  padding: 0 1rem;
}
</style>



